.hero-overlay{
    overflow: hidden;
    width: 100%;
    height: 502px;
    margin-left: auto;
    margin-right: auto;
    padding: 0;

    background-color: $accent-bg-pink;
    z-index: 1;
}

@media screen and (min-width: 768px) {
    .hero-overlay {
        height: 400px;
    }
}

@media screen and (min-width: 1280px) {
    .hero-overlay {
        height: 665px;
    }
}

.container--header{
    position: relative;
    align-items: center;
    height: 502px;
    padding-top: 5px;
    overflow: hidden;

    @media screen and (min-width: 768px) {
        padding-top: 35px;
        height: 400px;}

    @media screen and (min-width: 1280px) {
        height: 665px;}
}

.hero-bg--elipce{
    position: absolute;
    width: 350px;
    height: 350px;
    left: 16px;
    top: 83px;
    background-color: $secondary-text-accent;
    border-radius: 50%;
    z-index: 2;

    @media screen and (min-width: 768px) {
        width: 325px;
        height: 325px;
        left: 270px;
        top: 31px;}

    @media screen and (min-width: 1280px) {
        width: 538px;
        height: 538px;
        left: 360px;
        top: 52px;}
}

.hero-bg--ice{
    position: absolute;
    width: 170px;
    height: 410px;
    left: 97px;
    bottom: 0;

    z-index: 3;

    animation-name: bg-images-ice;
    animation-duration: 500ms;
    animation-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

    @media screen and (min-width: 768px) {
        position: absolute;
        width: 143px;
        height: 336px;
        left: 340px;
        bottom: 0;
    }

    @media screen and (min-width: 1280px) {
        width: 236px;
        height: 557px;
        left: 480px;
        bottom: 0;
    }
}

@keyframes bg-images-ice{
    0%{
        bottom: -1000px; }

    100%{
        bottom: 0; }
}

.hero-bg--milk{
    position: absolute;

    display: none;

    z-index: 3;

    @media screen and (min-width: 768px) {
        display: block;
        width: 124px;
        height: 126px;
        left: 0;
        top: 245px;

        animation-name: bg-images-milk;
        animation-duration: 500ms;
        animation-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    }

        @media screen and (min-width: 1280px) {
        width: 188px;
        height: 191px;
        left: 0;
        top: 426px;
    }
}

@keyframes bg-images-milk{
    0%{
        left: -1000px; }
    100%{
        left: 0; }
}

.hero-bg--woman{
    position: absolute;

    display: none;

    z-index: 3;

    animation-name: bg-images-woman;
    animation-duration: 500ms;
    animation-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

    @media screen and (min-width: 768px) {
        display: block;
        width: 168px;
        height: 133px;
        right: 0;
        bottom: 0;
    }

    @media screen and (min-width: 1280px) {
        width: 293px;
        height: 232px;
        right: 0;
        bottom: 0;
    }
}

@keyframes bg-images-woman{
    0%{
        right: -1000px; }
    100%{
        right: 0; }
}

.top {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;

    z-index: 10;
}

.logo__icon{
    width: 200px;
    height: 50px;
}

.menu-toggle.js-open-menu{
    background-color: transparent;
    border: none;
    cursor: pointer;

    margin-left: 60px;

    @media screen and (min-width: 768px) {
        margin-left: 300px;
    } 

    @media screen and (min-width: 1280px) {
        display: none;
    } 
   
}

.site-nav {
    display: flex;
    justify-content: space-between;

    @media screen and (max-width: 1279px){
        display: none;
    }
}

.site-nav__item {
    margin-left: 35px;

    &:first-child {
        margin-left: 0; 
    }
}

.site-nav__link{
    font-weight: 700;
    font-size: 16px;
    line-height: 21px;
    align-items: center;

    color: #FFFFFF;

    &:focus,
    &:hover{
        color: $contact-icon-hover;

        transition-property: color;
        transition-duration: 250ms;
        transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    }
}


.button-white--hero{
    display: none;
    width: 200px;
    height: 45px;
    align-items: center;
    justify-content: center;
    text-align: center;
    border: transparent;
    border-radius: 22px;

    &:focus,
    &:hover {
      transition-property: background-color;
      transition-duration: 250ms;
      transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    }
    
    @media screen and (min-width: 768px){
        display: inline-flex;
        width: 160px;
        right: 35px;
        padding: 0;

        margin-left: 30px;
    }

    @media screen and (min-width: 1280px) {
        margin-left: 0;
        
    } 

    .button-white--hero__icon{
        width: 10px;
        height: 14px;
        margin-left: 15px;
        stroke: #d41443;
    }

}

.container-hero{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;

    margin-top: 135px;

    z-index: 10;

    @media screen and (min-width: 768px){
        margin-top: 15px;
    }

    @media screen and (min-width: 1280px) {
        margin-left: 60px;
        margin-top: 85px;
    } 
}

.hero__title{
    font-weight: 400;
    font-size: 26px;
    line-height: 1.15;
    letter-spacing: 0.04em;
    text-transform: uppercase;

    color: #FFFFFF;

    @media screen and (min-width: 768px){
        font-size: 22px;
    }

    @media screen and (min-width: 1280px){
        font-size: 38px;
    }
}
.hero__title--accent{
    font-weight: 400;
    font-size: 48px;
    line-height: 0.9;

    text-transform: uppercase;

    color: #D41443;

    @media screen and (min-width: 768px){
        font-size: 34px;
    }
    @media screen and (min-width: 1280px){
        font-size: 58px;
    }
}

.hero-button{
    display: flex;
    margin-top: 20px;
}

.button--hero-products-pink{
    width: 60px;
    height: 24px;

    font-size: 8px;
    line-height: 1.25;
    align-items: center;
    text-align: center;
    border: transparent;

    &:focus,
    &:hover {
      transition-property: background-color;
      transition-duration: 250ms;
      transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    }

    @media screen and (min-width: 1280px){
        width: 100px;
        height: 40px;

        font-size: 14px;
    }
}

.button--hero-products-pink__link{
    color: white;
}

.button--hero-about-white{
    width: 80px;
    height: 24px;
    margin-left: 10px;

    font-size: 8px;
    line-height: 1.25;
    align-items: center;
    text-align: center;
    border: transparent;

    &:focus,
    &:hover {
      transition-property: background-color;
      transition-duration: 250ms;
      transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    }

    @media screen and (min-width: 1280px){
        width: 140px;
        height: 40px;

        font-size: 14px;
    }
}

.button--hero-about-white__link{
    color: $primary-text-accent;
}

.hero-statistic{
    display: none;
    margin-right: 16px;

    @media screen and (min-width: 768px){
        display: block;
        height: 50px;
    }
}

.hero-statistic__item{
    position: relative;
    margin-bottom: 15px;
    &:last-child{
        margin-bottom: 0;
    }
}

.hero-statistic__item--icon{
    position: absolute;
    width: 16px;
    height: 16px;
    border-radius: 50%;
    background-color: #fff;

    transform: translateX(40%);
    
    top: 0;
    right: 0;

    z-index: -1;

    @media screen and (min-width: 1280px){
        width: 30px;
        height: 30px;

    }
}

.hero-statistic__item--number{
    font-family: $accent-font;
    font-weight: 400;
    font-size: 28px;
    line-height: 1.14;

    text-align: right;
    letter-spacing: 0.04em;
    text-transform: uppercase;

    color: #D41443;

    @media screen and (min-width: 1280px){
        font-size: 46px;
    }
}

.hero-statistic__item--name{
    font-family: $main-font;
    font-weight: 400;
    font-size: 8px;
    line-height: 1.25;
    text-align: right;

    color: $color-main;

    @media screen and (min-width: 1280px){
        font-size: 14px;
    }
}

.hero-content{
    display: none;

    @media screen and (min-width: 768px){
        display: block;
        margin-top: 65px;
        margin-left: 115px;
    }

    @media screen and (min-width: 1280px){
        margin-top: 110px;
        margin-left: 180px;
    }
}

.hero-content__text{
    width: 115px;
    height: 50px;

    font-family: $main-font;
    font-weight: 400;
    font-size: 8px;
    line-height: 1.5;

    color: $color-main;

    @media screen and (min-width: 1280px){
        width: 200px;
        height: 80px;

        font-size: 14px;
        line-height: 1.4;
    }
}

.hero-content__icon{
    display: flex;
    padding: 0;
    background-color: $color-main;
    border: transparent;
    border-radius: 50%;
    width: 18px;
    height: 18px;
    cursor: pointer;

    align-items: center;
    justify-content: center;

    margin-top: 10px;

    &:hover,
    &:focus{
        transform: rotate(90deg);
        transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
    }

    @media screen and (min-width: 1280px){
        width: 26px;
        height: 26px;

        margin-top: 20px;
    }
}

.hero-content__icon-arrow{
    width: 6px;
    height: 6px;
    stroke: #d41443;

    @media screen and (min-width: 1280px){
        width: 10px;
        height: 10px;
    }
}

// mobile-menu

.menu-toggle.js-close-menu{
    position: absolute;
    top: 20px;
    right: 25px;
    background-color: transparent;
    border: none;
    cursor: pointer;
}

.menu-container{
    position: fixed;
    top: 0;
    right: 0;
    width: 250px;
    height: 100%;
    background-color: #E17992;
    box-shadow: none;
    
    z-index: 15;

    transform: translateX(100%);
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);

    &.is-open{
        box-shadow: -8px 0px 20px rgba(0, 0, 0, 0.25);
        transform: translateX(0);
    }

    @media screen and (min-width: 1280px) {
        transform: translateX(100%);
        box-shadow: none;
    }
}

.button-mobile-menu--hero{
    display: inline-flex;
    width: 190px;
    height: 45px;
    border: transparent;
    border-radius: 22px;
    align-items: center;
    justify-content: center;
    margin-top: 60px;
    margin-left: 35px;
    margin-right: 35px;

    &:focus,
    &:hover {
      transition-property: background-color;
      transition-duration: 250ms;
      transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    }
}

.button-mobile-menu--hero__icon{
    width: 10px;
    height: 14px;
    margin-left: 15px;
    stroke: #d41443;
}

.mobile-site-nav{
    margin-top: 20px;
}

.mobile--site-nav__item{
    border: 1px solid #E18298;
    padding: 10px 0 10px 24px;
}

.mobile-site-nav__link{
    font-weight: 700;
    font-size: 14px;
    line-height: 1.3;

    color: #FFFFFF;

    &:focus,
    &:hover{
        color: $contact-icon-hover;

        transition-property: color;
        transition-duration: 250ms;
        transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    }
}

// modal registration form

.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.2);

    opacity: 1;

    z-index: 20;

    &.is-hidden{
        opacity: 0;
        pointer-events: none;
    }
    
}

.backdrop .modal{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    width: 300px;
    height: 500px;
    border-radius: 4px;

    background-color: $accent-bg-pink;

    transition-property: transform;
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

    @media screen and (min-width: 480px) {
        width: 450px;
        height: 609px;
    }

    @media screen and (min-width: 1200px) {
        width: 528px;
        height: 581px;}

}

.backdrop.is-hidden .modal {
    top: 0;
    width: 0;
    height: 0;
}

.button-close{
    position: absolute;
    top: 8px;
    right: 8px;

    display: flex;
    padding: 6px;
    border: transparent;
    border-radius: 50%;
    background-color: transparent;
    align-items: center;
    justify-content: center;

    cursor: pointer;

    box-shadow: none;

    transition-property: fill;
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1); 
}

.button-close__icon{
    width: 18px;
    height: 18px;
}

/* modal registration form */

 .registrate-form{
    width: 300px;
    height: 500px;
    border-radius: 4px;
    padding: 10px;

    box-shadow: -8px 0px 20px rgba(0, 0, 0, 0.25);

    @media screen and (min-width: 480px) {
        width: 450px;
        height: 609px;
        padding: 40px;
    }

     @media screen and (min-width: 1200px) {
        width: 528px;
        height: 581px;
    }
     
}
.registrate-form__title{
    font-family: $accent-font;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 20px;
    line-height: 1.15;
    text-align: center;
    letter-spacing: 0.03em;

    margin-bottom: 12px;
    
    color: $primary-text-accent;
}

.registrate-form-field {
    position: relative;
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;

    // & textarea:hover,
    // & textarea:focus{
       
    //    border: 1px solid $slider-pointer;
    // }

    &:nth-child(5) {
        margin-bottom: 20px;
     }

    & textarea::placeholder{
        font-size: 14px;
        line-height: 1.14;
        letter-spacing: 0.01em;
    
        color: rgba(117, 117, 117, 0.5);
     }
 }
 
.registrate-form-field__input{
    width: 280px;
    height: 40px;
    border: 1px solid rgba(33, 33, 33, 0.2);
    box-sizing: border-box;
    border-radius: 4px;
    padding: 12px 30px;

    transition-property: border;
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

    // &:hover,
    // &:focus{       
    //    border: 1px solid $slider-pointer;
    // }

    @media screen and (min-width: 480px) {
        width: 370px;
    }

    @media screen and (min-width: 1200px) {
        width: 448px; }
 }

.registrate-form-field__lable{
    font: $main-font;
    margin-bottom: 4px;
    text-align: left;
    font-size: 14px;
    line-height: 1.17;
    letter-spacing: 0.01em;
    color: $primary-text-accent;
 }

textarea {
    resize: none;
    width: 280px;
    height: 120px;
    border: 1px solid rgba(33, 33, 33, 0.2);
    box-sizing: border-box;
    border-radius: 4px;  
    padding: 12px 16px;   

    @media screen and (min-width: 480px) {
        width: 370px;
    }

    @media screen and (min-width: 1200px) {
        width: 448px; }
}

.button--hero-products-modal{
    font-size: 14px;
    width: 120px;
    height: 40px;
    border: transparent;
}
